
require('dotenv').config();
const alchemyKey = process.env.REACT_APP_ALCHEMY_KEY;
const { createAlchemyWeb3 } = require("@alch/alchemy-web3");
const web3 = createAlchemyWeb3(alchemyKey);
const contractABI = require('../contract-abi.json')
const contractAddress = "0xBA4a122Cb6f7Cb07498F9bAf638e2B550a1F0346"; 
const price = 0.01;


const toHex = (num) => {
  return '0x'+num.toString(16)
}

export const addToNetwork = async () => {
  const params = {
    chainId: toHex(137), // A 0x-prefixed hexadecimal string
    chainName: "Polygon Mainet",
    nativeCurrency: {
      name: "MATIC",
      symbol:"MOMO", // 2-6 characters long
      decimals: 0,
    },
    rpcUrls: ["https://polygon-rpc.com/"],
    blockExplorerUrls: ["https://polygon-rpc.com/"]
  }
  try{
    const accounts = await window.ethereum.request({
      method: "eth_requestAccounts",
    });
    window.ethereum.request({
      method: 'wallet_addEthereumChain',
      params: [params, accounts[0]],
    })
    .then((result) => {
      console.log(result)
    })
    .catch((error) => {
      console.log(error)
    });
  }catch (err) {
    return {
      address: "",
      status: "üò• " + err.message,
    };
}};

export const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        const obj = {
          status: "",
          address: addressArray[0],
        };
        return obj;
      } catch (err) {
        return {
          address: "",
          status: "üò• " + err.message,
        };
      }
    } else {
      return {
        address: "",
        status: (
          <span>
            <p>
              {" "}
              ü¶ä{" "}
              <a target="_blank" href={`https://metamask.io/download.html`}>
                You must install Metamask, a virtual wallet, in your
                browser.
              </a>
            </p>
          </span>
        ),
      };
    }
  };

  export const mintNFT = async(amount) => {
     
    
    //set up your Ethereum transaction
    const transactionParameters = {
        gas: parseInt((258000*amount).toString()).toString(16),
        to: contractAddress, // Required except during contract publications.
        from: window.ethereum.selectedAddress, // must match user's active address.
        value: parseInt(web3.utils.toWei((price * amount).toString(),"ether")).toString(16),
        data: window.contract.methods.mint(window.ethereum.selectedAddress, amount).encodeABI() //make call to NFT smart contract 
    };

      
    //sign transaction via Metamask
    try {
        const txHash = await window.ethereum
            .request({
                method: 'eth_sendTransaction',
                params: [transactionParameters],
            });
        return {
            
            success: true,
            status: "‚úÖ Check out your transaction on Polygon: https://rinkeby.etherscan.io/tx/" + txHash
        }
    } catch (error) {
        return {
            success: false,
            status: "üò• Something went wrong: " + error.message
        }
    }
}

  export const getCurrentWalletConnected = async () => {
    //load smart contract
    window.contract = await new web3.eth.Contract(contractABI, contractAddress);//loadContract();
    
    //index = await window.contract.methods.totalSupply().call();

    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({
          method: "eth_accounts",
        });
        if (addressArray.length > 0) {
          return {
            address: addressArray[0],
            status: "",
          };
        } else {
          return {
            address: "",
            status: "ü¶ä Connect to Metamask using the connect button.",
          };
        }
      } catch (err) {
        return {
          address: "",
          status: "üò• " + err.message,
        };
      }
    } else {
      return {
        address: "",
        status: (
          <span>
            <p>
              {" "}
              ü¶ä{" "}
              <a target="_blank" href={`https://metamask.io/download.html`}>
                You must install Metamask, a virtual Ethereum wallet, in your
                browser.
              </a>
            </p>
          </span>
        ),
      };
    }
  };
